body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(121, 121, 250);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: 'Quicksand', sans-serif;
}
.form{min-width: 300px;
min-height: 300px;
background-color: #fff;
border-radius: 5px;
padding: 10px;


  form{
    display: flex;
    flex-direction: column;
justify-content: space-evenly;
align-items: center;
min-height: 300px;
.header{
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1px;
  
}.form-control{
    width: 250px;
    height: 40px;
    background-color: rgb(185, 181, 181);
    padding: 10px;
    border: none;
  }
  .invalid-feedback{
    color: red;
    font-size: 14px;
    font-weight: 700;
  }
  }.btn{
    width: 100px;
    height: 40px;
    background-color:  rgb(121, 121, 250);
    border: none;

  }.form-btn{
    width: 100%;
    display: flex;
    justify-content: space-around;
    button{
    cursor: pointer;
    &:hover{
      transform: scale(1.02);
      border-radius: 3px;
      box-shadow: 0 0 10px rgba(0,0,0,0.5);
      transition: 0.4s easy;
    }}
  }

}